	module.exports = {"data":{"post":{"html":"<p>Andrea and André are <a href=\"http://andrea.editthispage.com/2002/09/01\">back home</a> after an <a href=\"http://andrea.editthispage.com/2002/08/20\">eventful</a> trip to the American Southwest. Now, Mama and I had to go through a lot of trouble to have our Minnesota marriage certificate recognized here in Germany (the official in Kiel insisted that our certificate was improperly numbered). Forgive my curiosity, but are you going to have the American marriage recognized here, or are you going to have German ceremony at a <em>Standesamt</em>? Will a German civil servent have to determine whether <a href=\"http://hal.editthispage.com/\">Hal Rager</a> is really entitled to perform a wedding ceremony?</p>\n<p>And, another question, do either of you now have a hyphenated last name? What should we be calling you two now?</p>","fields":{"title":"Welcome Home,  and What's Your Name Now?","date":"01 Sep 2002","url":"https://papascott.de/2002-09-01-welcome-home-and-whats-your-name-now/"}}},"pathContext":{"slug":"/2002-09-01-welcome-home-and-whats-your-name-now/","prev":{"fields":{"title":"Spam Ping Pong With Der Schockwellenreiter","date":"01 Sep 2002","slug":"/2002-09-01-spam-ping-pong-with-der-schockwellenreiter/"}},"next":{"fields":{"title":"Where Am I Coming From","date":"01 Sep 2002","slug":"/2002-09-01-where-am-i-coming-from/"}}}}

